:root {
  --clr-item-bg: #353535;
  --clr-item-frnt: #fffdfd;
  --clr-item-acent: #f7df1e;
  --item-raduis: 1rem;
}
.skillsitemwrapper * {
  font-family: "Inter", sans-serif;
}

.skillsitemwrapper {
  font-size: 0.93rem;
  background: var(--clr-item-bg);
  width: 43vw;
  height: 13rem;
  border-bottom-right-radius: var(--item-raduis);
  border-top-right-radius: var(--item-raduis);
  display: grid;
  grid-template-columns: 30% auto;
  gap: 0.6rem;
}
.iconSide {
  color: var(--clr-item-bg);
  background: var(--clr-item-frnt);
  border-bottom-right-radius: var(--item-raduis);
  border-top-right-radius: var(--item-raduis);
  display: grid;
  place-items: center;
  place-content: center;
}
.icon {
  font-size: 4rem;
  color: var(--clr-item-acent);
}
.infoSide {
  padding: 1rem;
  display: flex;
  flex-direction: column;
}
.viewmore {
  color: var(--clr-item-acent);
  margin-top: auto;
  margin-left: auto;
  cursor: pointer;
}
@media screen and (max-width: 800px) {
  .skillsitemwrapper {
    font-size: 0.83rem;
    width: 90vw;
    grid-template-columns: 45% auto;
  }
}
