:root {
  --clr-item-bg: #353535;
  --clr-item-frnt: #fffdfd;
  --clr-item-acent: #f7df1e;
  --clr-item-button: #878787;
  --item-raduis: 1rem;
}
.wrrapper {
  display: grid;
  width: 24rem;
  height: 40rem;
  padding: 1rem;
  background-color: var(--clr-item-bg);
  grid-template-rows: 40% auto 10%;
  gap: 2rem;
}
.wrrapper img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.wrrapper p,
button {
  font: 1rem sans-serif;
  font-family: "Inter", sans-serif;
}
.buttons {
  display: flex;
  justify-content: space-between;
}
.wrrapper h1 {
  margin: 1rem 0;
}
.wrrapper button {
  height: 3rem;
  width: 45%;
  border-radius: var(--item-raduis);
  border: none;
  cursor: pointer;
}
.buttons button:nth-child(1) {
  color: var(--clr-item-frnt);
  background-color: var(--clr-item-button);
}

@media screen and (max-width: 800px) {
  .wrrapper {
    overflow: hidden;
    width: 90vw;
  }
}
